import styled from "styled-components";

export const Photo = styled.div`
	height: auto;
	position: relative;
	overflow: hidden;
	margin-bottom: 10px;
	img {
		width: 100%;
	}
	p {
		position: absolute;
		bottom: 5px;
		color: #999;
		background-color: #222;
		margin: 0;
		opacity: 0.8;
	}
`;
