import { Link } from "react-router-dom";
import styled from "styled-components";

export const ToHome = styled(Link)`
	width: 25%;
	display: inline-block;
	position: relative;
	border-left: 1px solid #274472;
	border-right: 1px solid #274472;
	img {
		width: 100%;
	}
	p {
		width: 100%;
		position: absolute;
		bottom: 0;
		margin: 0;
		text-align: center;
		color: #274472;
	}
	@media (min-width: 900px) {
		width: 200px;
	}
`;

export const ContainerToHome = styled.div`
	margin: 1rem 0;
	text-align: center;
`;
